import { useMemo } from 'react';
import { Box, Flex, Text } from '@chakra-ui/react';

import { Ratings, Tabs } from '../components';
import { ratingProfile } from "../models";

export const ProfileRatingContainer = () => {
	const tabs = useMemo(
		() =>
			ratingProfile.timePeriod.map((item) => ({
				label: item.periodName,
				component: <Ratings divisions={item.divisions} timePeriod={item.periodName} />,
			})),
		[ratingProfile.timePeriod],
	);

	return (
			<Box
				borderRadius="16px"
				bgColor="rgba(87, 92, 112, 0.3)"
				p={{
					base: '24px 16px',
					lg: '24px',
				}}
			>
				<Flex direction="column" gap="24px">
					<Text size="h24/32" variant="semibold">
						
					</Text>
						<Tabs tabs={tabs} />
				</Flex>
			</Box>
	);
};
